import * as echarts from "echarts";

function initChart(obj, elm) {

    if (obj == null) {
        obj = echarts.init(elm);
    }

    return obj;
}

function plotChart(obj, x, y, seriesName, seriesColor) {

    // specify chart configuration item and data
    var option = {
        title: {
            // text: seriesName
            subtext: seriesName,
            top: 0
        },

        tooltip: {},
        // legend: {
        //     data: [seriesName],
        // },
        xAxis: {
            data: x,
        },
        yAxis: {
            type: 'value'
        },
        series: [
            {
                name: seriesName,
                type: "line",
                data: y,
                smooth: true,
                showSymbol: false,
                color: seriesColor
            },
        ],
        grid: {
            top: "20%",
            bottom: "10%",
            left: "10%",
            right: "10%",
        }
    };
    obj.setOption(option);

}

export { initChart, plotChart };